import React, { Component } from 'react';

import ImageGallery from "../ImageGallery";
import Searchbar from '../Searchbar'
import Button from '../Button'
import Modal from '../Modal'
import API from '../serviceAPI/serviceAPI'

import { state } from './initialState'
import Loader from 'react-loader-spinner';

class Bery extends Component {

    state = {
        ...state
    }

    componentDidMount() {
        this.setState({loading:true})
    }
    
    componentDidUpdate() {
        const { loading} = this.state;

        if (loading) {
            this.fetchImages();
        }
    }

    onSubmit = value => {
        this.setState({
            images: [],
            searchQuery: value,
            page: 1,
            loading: true
        })
    }
    
    fetchImages = () => {
        
        const { searchQuery, page } = this.state;
        
        API.getAllImages(searchQuery,page)
            .then(res => {
                this.setState(({ page, images }) => {

                    return {
                        images: [...images, ...res],
                        page: page + 1,
                        loading: false,
                        url: ""
                    }
                })
            })
            .catch(error => error)
    }

    loadMore = () => {
        this.setState({loading:true})
    }
       
    toggleModal = img => {
        console.log(img)
        this.setState(({showModal}) => {
            this.setState({
                showModal: !showModal,
                activeImg: img,
            })
        })
    }
    
    render() {
        const { onSubmit,loadMore,toggleModal,imgModal } = this;
        const { images, showModal, activeImg } = this.state;
        console.log(activeImg)
        return (
            <>
                <Searchbar onSubmit = {onSubmit} />
                
                {this.state.loading && <Loader/>}
                <main>
                    <ImageGallery images={images} onImg={imgModal} onClick={toggleModal}/>
                    <Button onClick={loadMore} />
                </main>
                {showModal && <Modal onClose={toggleModal}>
                    <img src={activeImg} alt="foto"/>
                </Modal>}
            </>
            
        );
    }
}

export default Bery;