import styles from './ImageGalleryItem.module.css'

const ImageGalleryItem = ({ images,onClick }) => {
    const listEl = images.map(img => {
        return (
            <li key={img.id} className={styles.ImageGalleryItem} onClick={() => { onClick(img.largeImageURL)}}>
                <img src={img.webformatURL} alt={img.tags} className={styles.ImageGalleryItem_image}/>
            </li>    
        )
    })
    return <>
        {listEl}
    </>
};

export default ImageGalleryItem;