import axios from 'axios'

const URL = "https://pixabay.com/api/"
const KEY = "20951749-54c856bea8aa31576335fc08f"

const getAllImages = (tags, page) => {
    console.log(page)
    return axios.get(`${URL}?q=${tags}&page=${page}&key=${KEY}&image_type=photo&orientation=horizontal&per_page=12`, {
        params: {
            
        }
    })
        .then(res => res.data.hits)
}

// eslint-disable-next-line import/no-anonymous-default-export
export default { getAllImages };
